

.page-header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
}


.mobile-menu__burger-btn {
    background-color: var(--white-bg-color);
    border: 0;
    padding: 0;
    line-height: 0;
    display: block;
}


// слайдер
.header-switcher {
    align-items: center;
    gap: 10px;
    display: flex;
}
.header-switcher__item-light{
color: #fd5103;
}

.header-switcher__slider.round {
    width: 40px;
    height: 20px;
    background-color: #fff;
    border: 1px solid #fd5103;
    border-radius: 20px;
    position: absolute;
    cursor: pointer;
}

.header-switcher__switch{
    width: 40px;
    height: 20px;
    display: inline-block;
    position: relative;
}

.header-switcher input{
    opacity: 0;
    width: 0;
    height: 0;}

.header-switcher__slider.round::before {
    width: 12px;
    height: 12px;
    background-color: #fd5103;
    border-radius: 50%;
}

.header-switcher__slider::before {
    content: "";
    height: 26px;
    width: 26px;
    background-color: #fff;
    transition: all .4s;
    position: absolute;
    bottom: 3px;
    left: 2px;
}





// форма

.form-submit {
    width: 100%;
    max-width: 288px;
    height: 40px;
    border: 1px solid #fd5103 ;
    border-radius: 4px;
    transition-property: outline;
    transition-duration: .25s;
    padding: 8px;
    display: flex;
    align-items: center;
    margin-right: 40px;
}

.form-submit__input {
    max-width: 247px;
    border: none;
}

.form-submit__button {
    padding: 0%;
    border: none;
    background-color: rgb(255, 255, 255);
    padding-right: 8px;
    display: flex;
    align-items: center;

}

.form-submit__input {
    height: 24px;
    width: 100%;
    font-size: 14px;
    color: #5F6775;
    padding-left: 8px;
}

.form-submit__input:focus {
    outline: 0;
    outline-offset: 0;
}

.line {
    width: 1px;
    height: 40px;
    background-color: #fd5103;
}


// мобильное меню
.mobile-menu.is-hidden{
display: none;}


.mobile-menu {
    z-index: 8;
    padding: 27px 20px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fcfcfc;
    flex-direction: column;
    align-items: center;
    display: flex;
    width: 100%;
        height: 100%;
}


.header_logo-mobile{
    display: block;
    position: absolute;
    left: 20px
}

.mobile-menu__close-btn {
    z-index: 9;
    width: 47px;
    height: 39px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    display: block;
    position: absolute;
    top: 22px;
    right: 20px;
}

.site-nav__favorites.mobile {
    visibility: hidden;
}


.site-nav__favorites.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
}

.site-nav__favorites.mobile{
    z-index: 2;
    min-width: 213px;
    border: 1px solid #fd5103;
    border-radius: 4px;
    bottom: 0;
}
.site-nav-mobile__item{
padding-bottom:15px;}

.site-nav__item-menu{
padding:15px;
color: #000000;}






@media screen and (max-width: 767px) {
// логотип
.header_logo{
    padding-right: 125px;
}

.header-switcher{
display: none;}



.header__nav {
    display: none;
}

.header-switcher.mobile{
padding-bottom: 44px;
}
.mobile-menu{
display: flex;}


.header_logo-mobile{
display: none;}

.mobile-menu{
align-items:flex-start;
}

.form-submit{
display: none;}

.form-submit.mobile{
    margin-bottom: 40px;
}

.site-nav-mobile__link,
.site-nav-mobile__item{
    font-size: 28px;
    font-weight: 400;
    color: #000000;
}


.site-nav-mobile__item{
margin-top: 32px;}

.site-nav-mobile__arrow {
    align-items: inherit;
}

}







